import React, { useState, useEffect } from 'react'
import { 
  Dialog, 
  DialogContent, 
  DialogHeader, 
  DialogTitle,
  DialogDescription
} from '@/components/ui/dialog'
import { Badge } from '@/components/ui/badge'
import { Button } from '@/components/ui/button'
import { Card, CardContent } from '@/components/ui/card'
import { Separator } from '@/components/ui/separator'
import { 
  ShoppingCart, 
  Truck, 
  Package, 
  CheckCircle,
  Building2,
  Calendar,
  FileText,
  ArrowRight,
  User,
  CreditCard,
  MapPin,
  DollarSign
} from 'lucide-react'
import { useNavigate } from 'react-router-dom'
import { createClient } from '@/lib/supabase/client'
import { toast } from 'sonner'

interface PurchaseStatusModalProps {
  isOpen: boolean
  onClose: () => void
  item: any
  type: 'purchase' | 'delivery' | 'completed'
  onRefresh?: () => void
}

export default function PurchaseStatusModal({ 
  isOpen, 
  onClose, 
  item, 
  type,
  onRefresh
}: PurchaseStatusModalProps) {
  const navigate = useNavigate()
  const supabase = createClient()
  const [currentUserRoles, setCurrentUserRoles] = useState<string[]>([])
  const [processing, setProcessing] = useState(false)

  // Get current user roles
  useEffect(() => {
    const fetchUserRoles = async () => {
      const { data: { user } } = await supabase.auth.getUser()
      if (user?.email) {
        const { data: employee } = await supabase
          .from('employees')
          .select('purchase_role')
          .eq('email', user.email)
          .single()
        
        if (employee?.purchase_role) {
          const roles = employee.purchase_role.split(',').map((r: string) => r.trim())
          setCurrentUserRoles(roles)
        }
      }
    }
    fetchUserRoles()
  }, [])

  if (!item) return null

  const items = item.purchase_request_items || []
  const totalAmount = items.reduce((sum: number, i: any) => {
    return sum + (Number(i.amount_value) || 0)
  }, 0)
  const totalQuantity = items.reduce((sum: number, i: any) => {
    return sum + (Number(i.quantity) || 0)
  }, 0)

  const getTypeInfo = () => {
    switch (type) {
      case 'purchase':
        return {
          icon: <ShoppingCart className="w-6 h-6 text-yellow-600" />,
          title: '구매 대기',
          status: '구매 처리 대기중',
          color: 'bg-yellow-50 text-yellow-700 border-yellow-200'
        }
      case 'delivery':
        return {
          icon: <Truck className="w-6 h-6 text-blue-600" />,
          title: '입고 대기',
          status: '입고 처리 대기중',
          color: 'bg-blue-50 text-blue-700 border-blue-200'
        }
      case 'completed':
        return {
          icon: <CheckCircle className="w-6 h-6 text-green-600" />,
          title: '처리 완료',
          status: '모든 처리 완료',
          color: 'bg-green-50 text-green-700 border-green-200'
        }
    }
  }

  const typeInfo = getTypeInfo()

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="max-w-4xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle className="text-xl font-bold">
            {item.purchase_order_number || 'PO번호 없음'} 상세보기
          </DialogTitle>
          <DialogDescription>
            {item.vendor_name || '업체명 없음'}
          </DialogDescription>
        </DialogHeader>

        <div className="space-y-6">
          {/* 기본 정보 */}
          <div className="bg-gray-50 rounded-lg p-4">
            <h3 className="font-semibold mb-3 flex items-center">
              <FileText className="w-4 h-4 mr-2" />
              기본 정보
            </h3>
            <div className="grid grid-cols-2 gap-4">
              <div>
                <p className="text-sm text-gray-500">요청자</p>
                <p className="font-medium">{item.requester_name}</p>
              </div>
              <div>
                <p className="text-sm text-gray-500">요청일</p>
                <p className="font-medium">
                  {new Date(item.request_date || item.created_at).toLocaleDateString('ko-KR')}
                </p>
              </div>
              <div>
                <p className="text-sm text-gray-500">납기요청일</p>
                <p className="font-medium">
                  {item.delivery_request_date 
                    ? new Date(item.delivery_request_date).toLocaleDateString('ko-KR')
                    : '-'}
                </p>
              </div>
              <div>
                <p className="text-sm text-gray-500">카테고리</p>
                <p className="font-medium">{item.payment_category || '-'}</p>
              </div>
              <div>
                <p className="text-sm text-gray-500">상태</p>
                <p className="font-medium">
                  <Badge className={typeInfo.color}>
                    {typeInfo.title}
                  </Badge>
                </p>
              </div>
            </div>
          </div>

          {/* 품목 리스트 */}
          <div className="bg-gray-50 rounded-lg p-4">
            <h3 className="font-semibold mb-3 flex items-center">
              <Package className="w-4 h-4 mr-2" />
              품목 리스트
            </h3>
            <div className="overflow-x-auto">
              <table className="min-w-full">
                <thead className="bg-white">
                  <tr>
                    {type === 'delivery' && (
                      <th className="px-3 py-2 text-center text-xs font-medium text-gray-500">입고</th>
                    )}
                    <th className="px-3 py-2 text-left text-xs font-medium text-gray-500">품명</th>
                    <th className="px-3 py-2 text-left text-xs font-medium text-gray-500">규격</th>
                    <th className="px-3 py-2 text-center text-xs font-medium text-gray-500">수량</th>
                    <th className="px-3 py-2 text-right text-xs font-medium text-gray-500">단가</th>
                    <th className="px-3 py-2 text-right text-xs font-medium text-gray-500">금액</th>
                    <th className="px-3 py-2 text-left text-xs font-medium text-gray-500">비고</th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-200">
                  {items.map((pItem: any, index: number) => {
                    const unitPrice = pItem.quantity > 0 ? (Number(pItem.amount_value) || 0) / pItem.quantity : 0
                    return (
                      <tr key={index}>
                        {type === 'delivery' && (
                          <td className="px-3 py-2 text-center">
                            <div className="flex items-center justify-center">
                              {pItem.is_received ? (
                                <Badge className="bg-green-100 text-green-800 text-xs">
                                  입고완료
                                </Badge>
                              ) : (
                                <Badge variant="outline" className="text-gray-600 text-xs">
                                  미입고
                                </Badge>
                              )}
                            </div>
                          </td>
                        )}
                        <td className="px-3 py-2">
                          <span className="text-sm">{pItem.item_name || '품목명 없음'}</span>
                        </td>
                        <td className="px-3 py-2">
                          <span className="text-sm">{pItem.specification || '-'}</span>
                        </td>
                        <td className="px-3 py-2 text-center">
                          <span className="text-sm">{pItem.quantity || 0}</span>
                        </td>
                        <td className="px-3 py-2 text-right">
                          <span className="text-sm">₩{unitPrice.toLocaleString()}</span>
                        </td>
                        <td className="px-3 py-2 text-right">
                          <span className="text-sm">₩{(Number(pItem.amount_value) || 0).toLocaleString()}</span>
                        </td>
                        <td className="px-3 py-2">
                          <span className="text-sm">{pItem.remark || '-'}</span>
                        </td>
                      </tr>
                    )
                  })}
                </tbody>
              </table>
            </div>

            {/* 총액 */}
            <div className="mt-4 pt-4 border-t border-gray-200 bg-white p-3 rounded">
              <div className="flex justify-between items-center">
                <span className="font-semibold">총액</span>
                <span className="font-bold text-lg">₩{totalAmount.toLocaleString()}</span>
              </div>
            </div>
          </div>

          {/* 입고 진행률 (입고 대기인 경우) */}
          {type === 'delivery' && items.length > 1 && (
            <div className="bg-blue-50 rounded-lg p-4">
              <h4 className="text-sm font-medium text-gray-900 mb-3">입고 진행 현황</h4>
              {(() => {
                const receivedCount = items.filter((i: any) => i.is_received).length
                const totalCount = items.length
                const percentage = (receivedCount / totalCount) * 100
                
                return (
                  <div className="space-y-2">
                    <div className="flex justify-between text-sm">
                      <span className="text-gray-600">입고 완료</span>
                      <span className="font-medium text-gray-900">{receivedCount}/{totalCount}개 ({percentage.toFixed(0)}%)</span>
                    </div>
                    <div className="w-full bg-blue-200 rounded-full h-2 overflow-hidden">
                      <div 
                        className="bg-blue-600 h-2 rounded-full transition-all duration-300"
                        style={{ width: `${percentage}%` }}
                      />
                    </div>
                  </div>
                )
              })()}
            </div>
          )}

          {/* 버튼 영역 */}
          <div className="flex justify-between gap-3 mt-6">
            <div className="flex gap-2">
              {/* 입고 완료 버튼 - 입고 대기 상태일 때 */}
            {type === 'delivery' && (
              <Button
                onClick={async () => {
                  setProcessing(true)
                  try {
                    const { error } = await supabase
                      .from('purchase_requests')
                      .update({ 
                        is_received: true,
                        received_at: new Date().toISOString()
                      })
                      .eq('id', item.id)

                    if (error) throw error
                    
                    // 개별 품목도 모두 입고완료 처리
                    await supabase
                      .from('purchase_request_items')
                      .update({ 
                        is_received: true,
                        delivery_status: 'received'
                      })
                      .eq('purchase_request_id', item.id)
                    
                    toast.success('입고완료 처리되었습니다.')
                    onRefresh?.()
                    onClose()
                  } catch (error) {
                    toast.error('처리 중 오류가 발생했습니다.')
                  } finally {
                    setProcessing(false)
                  }
                }}
                disabled={processing}
                className="bg-blue-600 hover:bg-blue-700"
                size="sm"
              >
                {processing ? (
                  <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin mr-2" />
                ) : (
                  <CheckCircle className="w-4 h-4 mr-2" />
                )}
                입고 완료 처리
              </Button>
            )}

            {/* 구매 완료 버튼 - 구매 대기 상태이고 권한 있을 때 */}
            {type === 'purchase' && 
             (currentUserRoles.includes('app_admin') || 
              currentUserRoles.includes('lead_buyer') || 
              currentUserRoles.includes('final_approver') ||
              currentUserRoles.includes('ceo')) && (
              <Button
                onClick={async () => {
                  setProcessing(true)
                  try {
                    const { error } = await supabase
                      .from('purchase_requests')
                      .update({ 
                        is_payment_completed: true,
                        payment_completed_at: new Date().toISOString()
                      })
                      .eq('id', item.id)

                    if (error) throw error
                    
                    toast.success('구매완료 처리되었습니다.')
                    onRefresh?.()
                    onClose()
                  } catch (error) {
                    toast.error('처리 중 오류가 발생했습니다.')
                  } finally {
                    setProcessing(false)
                  }
                }}
                disabled={processing}
                className="bg-yellow-600 hover:bg-yellow-700"
                size="sm"
              >
                {processing ? (
                  <div className="w-4 h-4 border-2 border-white border-t-transparent rounded-full animate-spin mr-2" />
                ) : (
                  <CreditCard className="w-4 h-4 mr-2" />
                )}
                구매 완료 처리
              </Button>
            )}
            </div>

            <div className="flex gap-2">
              <Button
                variant="outline"
                onClick={() => {
                  navigate(`/purchase?highlight=${item.id}`)
                  onClose()
                }}
                size="sm"
              >
                발주 목록에서 보기
                <ArrowRight className="w-4 h-4 ml-2" />
              </Button>
              <Button variant="ghost" onClick={onClose} size="sm">
                닫기
              </Button>
            </div>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  )
}